.nav-tabs {
  float: right;
  margin-right: 10%;
}

.header {
  border-bottom: 3px solid #2220b4;
}

body {
  background-color: #fffdd0;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 600px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #6caddc;
  padding-top: 10px;
}
.card > .img-container > img {
  width: 90%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
  height: 800px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.port-card {
  height: 400px;
}

.portfolio-container {
  margin-top: 60px;
  margin-bottom: 100px;
}

.container {
  margin-left: 25%;
  border: 1px white solid;
  background-color: #fffdd0;
  width: 50%;
}

body {
  /* background-image: url('assets/img/0.jpg'); The image used */
  background-color: #cccccc; /* Used if the image is unavailable */
  min-height: 1300px; /* You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.nav-tabs {
  border: 2px lightgray solid;
}

.nav-link {
  color: white;
